import { fromJS, Map } from 'immutable';

import { mergeState, createImmutableState } from '../reducers';
import { IAction, ISubStateReducer } from '../../interfaces';
import {
  INormalizedCollectionState,
  INormalizedCollectionAction,
  INormalizedEntityAction,
  INormalizedEntityState,
} from './interfaces';
import { generateCrudActionNames } from './utils';

export function createNormalizedEntityState(): INormalizedEntityState {
  return createImmutableState({
    result: '',
    entities: Map() as any,
    loading: false,
    loaded: false,
    updating: false,
    updatedAt: 0,
    error: null,
  });
}

/**
 * This creates a reducer with full CRUD functionality for a single entity.
 *
 * Requirement: Data received from API is normalized, see https://github.com/paularmstrong/normalizr
 *
 * @export
 * @param  entityName
 * @param  [namespace]
 * @param [customReducers]
 * @returns
 */
export function normalizedEntityReducerFactory(
  entityName: string,
  namespace?: string,
  customReducers?: {
    [key: string]: (
      state: INormalizedEntityState,
      action: IAction
    ) => INormalizedEntityState;
  }
): ISubStateReducer<INormalizedEntityState> {
  const initialState: INormalizedEntityState = createNormalizedEntityState();

  return function(
    state: INormalizedEntityState = initialState,
    action: IAction
  ): INormalizedEntityState {
    const actionNames = generateCrudActionNames(entityName, namespace);

    const defaultReducers = {
      [actionNames.readOneStart]: (s: INormalizedEntityState, a: IAction) => {
        return mergeState(initialState, { loading: true });
      },
      [actionNames.readOneNext]: (
        s: INormalizedEntityState,
        a: INormalizedEntityAction
      ) => {
        return mergeState(s, {
          loading: false,
          loaded: true,
          updatedAt: Date.now(),
          entities: fromJS(a.payload.entities),
          result: a.payload.result,
        });
      },
      [actionNames.readOneError]: (s: INormalizedEntityState, a: IAction) => {
        return mergeState(initialState, {
          error: a.payload,
        });
      },
      [actionNames.createStart]: (s: INormalizedEntityState, a: IAction) => {
        return mergeState(s, { updating: true, error: null });
      },
      [actionNames.createNext]: (
        s: INormalizedEntityState,
        a: INormalizedEntityAction
      ) => {
        return mergeState(s, {
          updating: false,
          loaded: true,
          updatedAt: Date.now(),
          entities: fromJS(a.payload.entities),
          result: a.payload.result,
        });
      },
      [actionNames.createError]: (s: INormalizedEntityState, a: IAction) => {
        return mergeState(initialState, { updating: false, error: a.payload });
      },
      [actionNames.updateStart]: (s: INormalizedEntityState, a: IAction) => {
        return mergeState(s, { updating: true, error: null });
      },
      [actionNames.updateNext]: (
        s: INormalizedEntityState,
        a: INormalizedEntityAction
      ) => {
        return mergeState(s, {
          updating: false,
          loaded: true,
          updatedAt: Date.now(),
          entities: fromJS(a.payload.entities),
          result: a.payload.result,
        });
      },
      [actionNames.updateError]: (s: INormalizedEntityState, a: IAction) => {
        return mergeState(s, { updating: false, error: a.payload });
      },
      [actionNames.deleteStart]: (s: INormalizedEntityState, a: IAction) => {
        return mergeState(s, { updating: true, error: null });
      },
      [actionNames.deleteNext]: (s: INormalizedEntityState, a: IAction) => {
        return initialState;
      },
      [actionNames.deleteError]: (s: INormalizedEntityState, a: IAction) => {
        return mergeState(s, { updating: false, error: a.payload });
      },
      [actionNames.setCurrent]: (
        s: INormalizedEntityState,
        a: INormalizedEntityAction
      ) => {
        return mergeState(initialState, {
          loaded: true,
          entities: fromJS(a.payload.entities),
          result: a.payload.result,
        });
      },
      [actionNames.reset]: (s: INormalizedEntityState, a: IAction) => {
        return initialState;
      },
      [actionNames.resetError]: (s: INormalizedEntityState, a: IAction) => {
        return s.set('error', null);
      },
    };

    const reducers = Object.assign({}, defaultReducers, customReducers);

    if (reducers[action.type]) {
      return reducers[action.type](state, action);
    }

    return state;
  };
}

export function createNormalizedCollectionState(): INormalizedCollectionState {
  return createImmutableState({
    result: [] as any,
    entities: {} as any,
    loading: false,
    loaded: false,
    updating: false,
    updatedAt: 0,
    error: null,
  });
}

/**
 * This creates a reducer with full CRUD functionality for a collection of entities.
 *
 * Requirement: Data received from API is normalized, see https://github.com/paularmstrong/normalizr
 *
 * @export
 * @param  entityName
 * @param  [namespace]
 * @param [customReducers]
 * @returns
 */
export function normalizedCollectionReducerFactory(
  entityName: string,
  namespace?: string,
  customReducers?: {
    [key: string]: (
      state: INormalizedCollectionState,
      action: IAction
    ) => INormalizedCollectionState;
  }
): ISubStateReducer<INormalizedCollectionState> {
  const initialState: INormalizedCollectionState = createNormalizedCollectionState();

  return function(
    state: INormalizedCollectionState = initialState,
    action: IAction
  ): INormalizedCollectionState {
    const actionNames = generateCrudActionNames(entityName, namespace);

    const defaultReducers = {
      [actionNames.readStart]: (s: INormalizedCollectionState, a: IAction) => {
        return mergeState(initialState, { loading: true });
      },
      [actionNames.readNext]: (
        s: INormalizedCollectionState,
        a: INormalizedCollectionAction
      ) => {
        return mergeState(s, {
          loading: false,
          loaded: true,
          updatedAt: Date.now(),
          entities: fromJS(action.payload.entities),
          result: action.payload.result,
        });
      },
      [actionNames.readError]: (s: INormalizedCollectionState, a: IAction) => {
        return mergeState(initialState, { error: a.payload });
      },
      [actionNames.createStart]: (
        s: INormalizedCollectionState,
        a: IAction
      ) => {
        return mergeState(s, { updating: true, error: null });
      },
      [actionNames.createNext]: (
        s: INormalizedCollectionState,
        a: INormalizedEntityAction
      ) => {
        return mergeState(s, {
          updating: false,
          entities: s.get('entities').mergeDeep(fromJS(a.payload.entities)),
          result: s.get('result').push(a.payload.result),
        });
      },
      [actionNames.createError]: (
        s: INormalizedCollectionState,
        a: IAction
      ) => {
        return mergeState(s, {
          updating: false,
          error: a.payload,
        });
      },
      [actionNames.updateStart]: (
        s: INormalizedCollectionState,
        a: IAction
      ) => {
        return mergeState(s, { updating: true, error: null });
      },
      [actionNames.updateNext]: (
        s: INormalizedCollectionState,
        a: INormalizedEntityAction
      ) => {
        return mergeState(s, {
          updating: false,
          entities: s.get('entities').mergeDeep(fromJS(a.payload.entities)),
        });
      },
      [actionNames.updateError]: (
        s: INormalizedCollectionState,
        a: IAction
      ) => {
        return mergeState(s, {
          updating: false,
          error: a.payload,
        });
      },
      [actionNames.deleteStart]: (
        s: INormalizedCollectionState,
        a: IAction
      ) => {
        return mergeState(s, { updating: true, error: null });
      },
      [actionNames.deleteNext]: (
        s: INormalizedCollectionState,
        a: INormalizedEntityAction
      ) => {
        return mergeState(s, {
          updating: false,
          entities: s.get('entities').deleteIn([entityName, a.payload.result]),
          result: s
            .get('result')
            .update(ids => ids.filterNot(id => id === a.payload.result)),
        });
      },
      [actionNames.deleteError]: (
        s: INormalizedCollectionState,
        a: IAction
      ) => {
        return mergeState(s, {
          updating: false,
          error: a.payload,
        });
      },
      [actionNames.reset]: (s: INormalizedCollectionState, a: IAction) => {
        return initialState;
      },
      [actionNames.resetError]: (s: INormalizedCollectionState, a: IAction) => {
        return s.set('error', null);
      },
    };

    const reducers = Object.assign({}, defaultReducers, customReducers);

    if (reducers[action.type]) {
      return reducers[action.type](state, action);
    }

    return state;
  };
}
