import { Middleware, GenericStoreEnhancer } from 'redux';
import { NgRedux, DevToolsExtension } from '@angular-redux/store';
import { NgReduxRouter } from '@angular-redux/router';
import { Map, fromJS } from 'immutable';

import { IState, IEnvironment } from '../interfaces';
import { RootReducer } from '../redux/root.reducer';
import { RootEpic } from '../redux/root.epic';
import { StableService } from '../services/stable.service';
import { createReduxStore, routerStateSelector } from '../redux/store';
import {
  observableMiddleware,
  removeEmptyActionsMiddleware,
} from '../redux/middleware';

/**
 * Base class for an Angular Root Module.
 *
 * This sets up all the basics like redux store, stable tracking, hmr in dev ...
 *
 * @export
 * @abstract
 */
export abstract class MainBaseModule {
  constructor(
    private store: NgRedux<IState>,
    private devTools: DevToolsExtension,
    private rootReducer: RootReducer,
    private rootEpic: RootEpic,
    private ngReduxRouter: NgReduxRouter,
    private environment: IEnvironment,
    private stableService: StableService
  ) {
    let appState: IState | undefined;

    // @ts-ignore
    if (module.hot && window.hmrData) {
      // @ts-ignore
      appState = window.hmrData.appState;
      // @ts-ignore
      delete window.hmrData;
    }

    this.setupStore(appState);
    this.ngReduxRouter.initialize(routerStateSelector);
  }

  protected setupStore(appState: IState = Map({})): void {
    const store = createReduxStore(
      <any>this.rootReducer.reducer(),
      fromJS(appState),
      this.getMiddleware(),
      this.getEnhancers()
    );

    this.store.provideStore(store);
    this.stableService.init();
  }

  protected getMiddleware(): Middleware[] {
    let defaultMiddleware = [
      removeEmptyActionsMiddleware,
      observableMiddleware,
      this.rootEpic.middleware,
    ];

    if (this.environment.additionalMiddleware) {
      defaultMiddleware = [
        ...defaultMiddleware,
        ...this.environment.additionalMiddleware,
      ];
    }

    return defaultMiddleware;
  }

  protected getEnhancers(): GenericStoreEnhancer[] {
    let defaultEnhancers: GenericStoreEnhancer[] = [];

    if (this.environment.additionalEnhancers) {
      defaultEnhancers = [
        ...defaultEnhancers,
        ...this.environment.additionalEnhancers,
      ];
    }

    if (!this.environment.production && this.devTools.isEnabled()) {
      defaultEnhancers = [...defaultEnhancers, this.devTools.enhancer()];
    }

    return defaultEnhancers;
  }
}
