import { List, fromJS } from 'immutable';
import { Constructor } from '../interfaces';

export function EmbeddedRecord(recordClass: any) {
  return function(target: any, key: string): any {
    const cache = new WeakMap();

    return {
      get() {
        if (!cache.has(this)) {
          cache.set(this, new recordClass(this.get(key)));
        }
        return cache.get(this);
      }
    };
  };
}

export function EmbeddedCollection(
  recordClass: any,
  collectionFactory: any = List
) {
  return function(target: any, key: string): any {
    const cache = new WeakMap();

    return {
      get() {
        if (!cache.has(this)) {
          const collection = this.get(key) || collectionFactory();
          cache.set(
            this,
            collectionFactory(
              collection.map((data: any) => new recordClass(data))
            )
          );
        }
        return cache.get(this);
      }
    };
  };
}

export function ExtendedRecord<TBase extends Constructor<{}>>(Base: TBase) {
  type Params = Partial<TBase>;

  return class extends Base {
    public merge: any;

    constructor(...args: any[]) {
      const params: Params = fromJS(args.shift());
      super(params, ...args);
    }

    public with(params: Params) {
      return this.merge(fromJS(params)) as this;
    }
  };
}
