import { Injectable } from '@angular/core';
import { dispatch } from '@angular-redux/store';

import { IAction } from '../interfaces';

export const TRANSLATE_SET_LOCALE: string = 'TRANSLATE_SET_LOCALE';

@Injectable()
export class TranslateActions {
  @dispatch()
  public setLocale(locale: string): IAction {
    return {
      type: TRANSLATE_SET_LOCALE,
      payload: locale
    };
  }
}
