import { FormGroup, FormControl } from '@angular/forms';
import { Map } from 'immutable';

/**
 * Base class for *dumb* components
 *
 * Data is transfered via Inputs and Outputs, no Store or Actions allowed.
 *
 * @export
 */
export class PresentationalComponent {
  /**
   * ImmutableJS aware track function
   *
   * Mainly used in ngFor trackBy
   *
   * @param  index
   * @param  item
   * @returns
   *
   * @memberOf PresentationalComponent
   */
  public identify(_: number, item: Map<string, any>): number {
    if (typeof item.hashCode !== 'function') {
      throw new TypeError(
        `Given item is not of type Immutable, ${item.toString()} given!`
      );
    }
    return item.hashCode();
  }

  /**
   * Tests, wether a form component has errors
   *
   * @param  form
   * @param  fieldName
   * @param  [errorName]
   * @returns
   *
   * @memberOf PresentationalComponent
   */
  public hasError(
    form: FormGroup,
    fieldName: string,
    errorName?: string
  ): boolean {
    const control: FormControl = <FormControl>form.get(fieldName);

    if (control.untouched) {
      return false;
    }

    if (errorName) {
      return !!(control.errors && control.errors[errorName]);
    } else {
      return !!control.errors;
    }
  }
}
