import { Injectable, Inject } from '@angular/core';
import {
  combineEpics,
  ActionsObservable,
  createEpicMiddleware,
  EpicMiddleware,
} from 'redux-observable';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/mergeMap';
import 'rxjs/add/observable/empty';

import { IAction, IState, IAppEpic, IEpic, IEpicClass } from '../interfaces';
import { APP_EPICS } from '../tokens';

// create one noop epic to fill provider with at least one value
// for epics docs see https://redux-observable.js.org/
export function dummyEpic(action$: ActionsObservable<IAction>) {
  return action$.mergeMap(() => {
    return Observable.empty();
  });
}

/**
 * Wrapper class to activate all injected [[Epic]] instances and forward them to the redux-observable middleware.
 *
 * @export
 */
@Injectable()
export class RootEpic {
  public middleware: EpicMiddleware<IAction, IState>;

  constructor(@Inject(APP_EPICS) private epicConfigs: IAppEpic[]) {
    const epics = this.buildEpics(epicConfigs);
    this.middleware = createEpicMiddleware(combineEpics(...epics));
  }

  /**
   * Returns the Root Epic given to the middleware
   *
   * @returns
   * @memberof RootEpic
   */
  public epic(): IAppEpic {
    return combineEpics(...this.epicConfigs);
  }

  /**
   * This method collects the [[Epic]] functions from the injected APP_EPICS and extracts the function
   * from [[IEpicClass]] if given.
   *
   * @protected
   * @param  epicConfigs
   * @returns
   * @memberof RootEpic
   */
  protected buildEpics(epicConfigs: IAppEpic[]): IEpic[] {
    return epicConfigs.map(config => {
      if ((<IEpicClass>config).epic) {
        return (<IEpicClass>config).epic();
      } else {
        return config as IEpic;
      }
    });
  }
}
