import { denormalize, Schema } from 'normalizr';
import { Selector } from 'reselect';
import { List, Map, Record } from 'immutable';

import { Constructor, IState } from '../../interfaces';
import {
  INormalizedCollectionSelectors,
  INormalizedCollectionState,
  INormalizedEntityState,
  INormalizedEntitySelectors,
} from './interfaces';
import { createSelector } from '../reselect';

/**
 * Creates selectors for a reducer based on a [[INormalizedEntityState]].
 *
 * Provides base selectors for all attributes and also for building a [[Record]] instance out of the raw data
 *
 * @export
 * @param  entityName
 * @param  entityClass The [[Record.Class]] class to build the model from
 * @param  schema The same schema that was used no normalize the data in the first place
 * @param ]
 * @returns
 */
export function normalizedEntitySelectorFactory<T extends Record<any>>(
  entityName: string,
  entityClass: Constructor<T>,
  schema: Schema,
  customSelectors: { [key: string]: Selector<IState, any> } = {}
): INormalizedEntitySelectors<T> {
  const baseSelector = (state: IState): INormalizedEntityState =>
    state.get(entityName);
  const idSelector = (state: IState): string =>
    baseSelector(state).get('result');
  const entitiesSelector = (state: IState): Map<string, Map<string, any>> =>
    baseSelector(state).get('entities');

  const modelSelector = createSelector(
    [idSelector, entitiesSelector],
    (id: string, entities: Map<string, Map<string, Map<string, any>>>) => {
      if (id) {
        const denormalizedData = denormalize(id, schema, entities);
        return new entityClass(denormalizedData);
      } else {
        return new entityClass();
      }
    }
  );

  return Object.assign(
    {},
    {
      subStateSelector: baseSelector,
      loadingSelector: (state: IState): boolean =>
        baseSelector(state).get('loading'),
      loadedSelector: (state: IState): boolean =>
        baseSelector(state).get('loaded'),
      updatingSelector: (state: IState): boolean =>
        baseSelector(state).get('updating'),
      updatedAtSelector: (state: IState): number =>
        baseSelector(state).get('updatedAt'),
      idSelector,
      modelSelector,
    },
    customSelectors
  );
}

/**
 * Creates selectors for a reducer based on a [[INormalizedCollectionState]].
 *
 * Provides base selectors for all attributes and also for building a collection of [[Record]] instances out of the raw data
 *
 * @export
 * @param  entityName
 * @param  entityClass The [[Record.Class]] class to build the models from
 * @param  schema The same schema that was used no normalize the data in the first place
 * @param ]
 * @returns
 */
export function normalizedCollectionSelectorsFactory<T extends Record<any>>(
  entityName: string,
  entityClass: Constructor<T>,
  schema: Schema,
  customSelectors: { [key: string]: Selector<IState, any> } = {}
): INormalizedCollectionSelectors<T> {
  const baseSelector = (state: IState): INormalizedCollectionState =>
    state.get(entityName);
  const idsSelector = (state: IState): List<string> =>
    baseSelector(state).get('result');
  const entitiesSelector = (state: IState): Map<string, Map<string, any>> =>
    baseSelector(state).get('entities');

  const modelsSelector = createSelector(
    [idsSelector, entitiesSelector],
    (
      ids: List<string>,
      entities: Map<string, Map<string, Map<string, any>>>
    ) => {
      const denormalizedData = denormalize(ids, schema, entities);
      return denormalizedData.map(
        (item: Map<string, any>) => new entityClass(item)
      );
    }
  );

  const modelsMapSelector = createSelector([modelsSelector], models => {
    return models.reduce((acc: Map<string, T>, item: T) => {
      return acc.set(String(item.get('id', null)), item);
    }, Map());
  });

  return Object.assign(
    {},
    {
      subStateSelector: baseSelector,
      loadingSelector: (state: IState): boolean =>
        baseSelector(state).get('loading'),
      loadedSelector: (state: IState): boolean =>
        baseSelector(state).get('loaded'),
      updatingSelector: (state: IState): boolean =>
        baseSelector(state).get('updating'),
      updatedAtSelector: (state: IState): number =>
        baseSelector(state).get('updatedAt'),
      idsSelector,
      modelsSelector,
      modelsMapSelector,
    },
    customSelectors
  );
}
