import { Injectable, Inject } from '@angular/core';
import { combineReducers } from 'redux-immutable';
import { enableBatching } from 'redux-batched-actions';

import { IReducer, IReducerConfig, IReducerClass } from '../interfaces';
import { APP_REDUCERS } from '../tokens';

/**
 * Class that builds the root reducer for redux from all injected [[APP_REDUCERS]].
 *
 * @export
 */
@Injectable()
export class RootReducer {
  private currentReducers: any = {};

  constructor(@Inject(APP_REDUCERS) private reducerConfigs: IReducerConfig[]) {}

  /**
   *
   *
   * @param  [newReducerConfigs]
   * @returns
   * @memberof RootReducer
   */
  public reducer(newReducerConfigs?: IReducerConfig[]): IReducer {
    return enableBatching(
      combineReducers(this.buildReducers(newReducerConfigs))
    ) as IReducer;
  }

  /**
   * Builds the root reducer.
   *
   * @param  [newReducerConfigs]
   * @returns
   * @memberof RootReducer
   */
  public buildReducers(newReducerConfigs?: IReducerConfig[]) {
    const currentReducers = Object.assign({}, this.currentReducers);
    const reducerConfigs: IReducerConfig[] = newReducerConfigs
      ? newReducerConfigs
      : this.reducerConfigs;

    this.currentReducers = reducerConfigs.reduce((r, config) => {
      if (typeof (config.reducer as IReducerClass).reducer === 'function') {
        r[config.name] = (config.reducer as IReducerClass).reducer();
      } else {
        r[config.name] = config.reducer;
      }

      return r;
    }, currentReducers);

    return this.currentReducers;
  }
}
