import { Injectable, Inject } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Schema, normalize } from 'normalizr';
// @ts-ignore
import Ajv from 'ajv';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/share';

import { IEnvironment } from '../interfaces';
import { APP_ENVIRONMENT } from '../tokens';

/**
 * Wrapper class around Angular [[Http]].
 *
 * Adds features like schema validation, normalizing data, base API URL,
 * tracking the number of active requests ...
 *
 * @export
 */
@Injectable()
export class RestService {
  // tslint:disable-next-line:variable-name
  protected _activeRequests: number = 0;

  get activeRequests(): number {
    return this._activeRequests;
  }

  set activeRequests(num: number) {
    this._activeRequests = num;
  }

  constructor(
    protected http: HttpClient,
    @Inject(APP_ENVIRONMENT) protected environment: IEnvironment
  ) {}

  /**
   * HTTP GET
   *
   * @param  path
   * @param [options]
   * @param  [schema]
   * @param  [jsonSchemaValidator]
   * @returns
   * @memberof RestService
   */
  public get(
    path: string,
    options: any = {},
    schema?: Schema,
    jsonSchemaValidator?: Ajv.ValidateFunction
  ): Observable<any> {
    return this.request('get', path, options, schema, jsonSchemaValidator);
  }

  /**
   * HTTP POST
   *
   * @param  path
   * @param  body
   * @param [options]
   * @param [schema]
   * @param [jsonSchemaValidator]
   * @returns
   * @memberof RestService
   */
  public post(
    path: string,
    body: any,
    options: any = {},
    schema?: Schema,
    jsonSchemaValidator?: Ajv.ValidateFunction
  ): Observable<any> {
    options = { ...options, body };
    return this.request('post', path, options, schema, jsonSchemaValidator);
  }

  /**
   * HTTP PUT
   *
   * @param  path
   * @param  body
   * @param [options]
   * @param  [schema]
   * @param  [jsonSchemaValidator]
   * @returns
   * @memberof RestService
   */
  public put(
    path: string,
    body: any,
    options: any = {},
    schema?: Schema,
    jsonSchemaValidator?: Ajv.ValidateFunction
  ): Observable<any> {
    options = { ...options, body };
    return this.request('put', path, options, schema, jsonSchemaValidator);
  }

  /**
   * HTTP DELETE
   *
   * @param  path
   * @param [options]
   * @param  [schema]
   * @param  [jsonSchemaValidator]
   * @returns
   * @memberof RestService
   */
  public delete(
    path: string,
    options: any = {},
    schema?: Schema,
    jsonSchemaValidator?: Ajv.ValidateFunction
  ): Observable<any> {
    return this.request('delete', path, options, schema, jsonSchemaValidator);
  }

  /**
   * Base method called by the other get/post... methods
   *
   * @protected
   * @param  path
   * @param  requestOptions
   * @param  [schema]
   * @param  [jsonSchemaValidator]
   * @returns
   * @memberof RestService
   */
  protected request(
    type: string,
    path: string,
    requestOptions: any,
    schema?: Schema,
    jsonSchemaValidator?: Ajv.ValidateFunction
  ): Observable<any> {
    const url: string = this.getUrl(path);
    requestOptions = this.buildRequestOptions(requestOptions);
    this.activeRequests++;

    return this.http
      .request(type, url, requestOptions)
      .map((data: any) => {
        if (jsonSchemaValidator) {
          const valid = jsonSchemaValidator(data);
          if (!valid) {
            this.logSchemaErrors(
              jsonSchemaValidator.errors,
              data,
              path,
              requestOptions
            );
            if (this.environment.throwOnSchemaError) {
              throw new Error('Schema validation failed');
            }
          }
        }
        return data;
      })
      .map((data: any) => this.normalizePayload(data, schema))
      .map((data: any) => {
        this.activeRequests--;
        return data;
      })
      .catch((error: HttpErrorResponse) => {
        this.activeRequests--;
        throw error;
      })
      .share();
  }

  protected logSchemaErrors(
    errors: Ajv.ErrorObject[] | null | undefined,
    responseData: any,
    path: string,
    requestOptions: any
  ): void {
    if (errors && errors.length) {
      const css =
        'color: red; background-color: #ffe6e6; border: 1px solid #ff9999; padding: 1px 10px';
      console.group('%cSchema validation failed', css);
      console.log('Request:', path, requestOptions);
      console.log('Response Data:', responseData);
      console.group('%cErrors:', css);
      errors.forEach(error => {
        console.log(error);
      });
      console.groupEnd();
      console.groupEnd();
    }
  }

  protected getUrl(path: string): string {
    let url: string;
    if (path.startsWith('http') || path.startsWith('//')) {
      url = path;
    } else {
      url = `${this.environment.apiUrl}/${path}`;
    }

    return url;
  }

  /**
   * If a normalizr schema is given, it returns the normlaized date, the original data otherwise.
   *
   * @param  data
   * @param  [schema]
   * @returns either normalized or the given data.
   */
  protected normalizePayload(data: any, schema?: Schema) {
    if (schema) {
      return normalize(data, schema);
    }

    return data;
  }

  /**
   * Extracts the JSON from an Angular [[Response]]
   *
   * @param  response
   * @returns Whatever the HTTP call returned as JSON
   */
  protected extractResponseData(response: Response): any {
    let payload: any;

    try {
      payload = response.json();
    } catch (e) {
      payload = {};
    }

    return payload;
  }

  /**
   * Hook to do some processing of the given options
   *
   * Used mainly to add headers (like auth) or other default options to all requests.
   * Just returns the given options as default
   *
   * @param options
   */
  protected buildRequestOptions(options: any): any {
    return options;
  }
}
