import {
  applyMiddleware,
  compose,
  createStore,
  Store,
  Reducer,
  Middleware,
  GenericStoreEnhancer,
} from 'redux';
import { fromJS } from 'immutable';
import persistState from 'redux-localstorage';
import transit from 'transit-immutable-js';

import { IState } from '../interfaces';

/**
 * Helper function to setup the redux store with reducers, middleware and enhancers.
 *
 * @export
 * @param  rootReducer
 * @param )]
 * @param  [middleware=[]]
 * @param  [enhancers=[]]
 * @returns
 */
export function createReduxStore(
  rootReducer: Reducer<IState>,
  initialState: IState = fromJS({}),
  middleware: Middleware[] = [],
  enhancers: GenericStoreEnhancer[] = []
): Store<IState> {
  return createStore(
    rootReducer,
    initialState,
    compose(applyMiddleware(...middleware), ...enhancers)
  ) as Store<IState>;
}

/**
 * Redux [[GenericStoreEnhancer]] that persists all state changes to localStorage,
 * knowing how to handle an ImmutableJS state.
 *
 * @export
 * @returns
 */
export function persistStateEnhancer(
  paths?: string | string[]
): GenericStoreEnhancer {
  const converter = transit.withFilter((value: any) => {
    return !(value instanceof Error);
  });

  return persistState(paths, {
    serialize: (state: IState) => converter.toJSON(state),
    deserialize: (data: string) => converter.fromJSON(data),
    merge: (initialState: IState, persistedState: IState) =>
      initialState.merge(persistedState),
    slicer: (statePaths: undefined | string | string[]) => {
      return (state: IState) => {
        if (typeof statePaths === 'string') {
          return state.filter((_, k) => k === statePaths);
        } else if (Array.isArray(statePaths)) {
          return state.filter((_, k) => statePaths.indexOf(k) !== -1);
        } else {
          return state;
        }
      };
    },
  });
}

/**
 * Selector function to return the current router substate.
 *
 * @export
 * @param  state
 * @returns string
 */
export function routerStateSelector(state: IState): string {
  return state.get('router');
}
