import { NgModule } from '@angular/core';
import { routerReducer } from '@angular-redux/router';

import { RootReducer } from './root.reducer';
import { RootEpic, dummyEpic } from './root.epic';
import { APP_EPICS, APP_REDUCERS } from '../tokens';

@NgModule({
  providers: [
    RootReducer,
    RootEpic,
    {
      multi: true,
      provide: APP_REDUCERS,
      useValue: { name: 'router', reducer: routerReducer },
    },
    { provide: APP_EPICS, useValue: dummyEpic, multi: true },
  ],
})
export class ReduxModule {}
